.header {
	display: flex;
	flex-flow: wrap;
	justify-content: space-between;

	background-color: $header-background-color;
	&-logo {
		@include respond-to(tablets) {
			order: 1;
			margin: 18px;

			background: url('../images/logo-tab-min.png') no-repeat;
		}

		width: 325px;
		margin: 30px 50px;

		background: url('../images/logo-min.png') no-repeat;
		a {
			color: transparent;
			font-size: 50px;
			letter-spacing: 12px;
			word-spacing: 7px;
		}
	}
}

.logo-img {
	width: 100%;
	height: auto;
}

.switcher {
	@include respond-to(tablets) {
		order: 3;
		margin-top: -48px;
		margin-left: 20px;
	}

	margin-top: 88px;
	&__item {
		display: inline-block;
		padding: 10px;
		padding-right: 35px;

		background-color: $switcher-background-color;

		color: $primary-text-color;
		font-weight: 300;
		text-decoration: none;
		vertical-align: bottom;
		&:hover {
			text-decoration: underline;
		}
		&:last-child {
			position: relative;

			padding: 15px 30px;

			background-color: $switcher-contrast-background-color;

			color: $white;
			&::before {
				content: "";

				position: absolute;
				top: 0;
				right: 100%;

				width: 0;
				height: 0;
				border-bottom: 48px solid $switcher-contrast-background-color;
				border-left: 27px solid transparent;
			}
		}
		span {
			font-size: $switcher-font-size;
			text-transform: uppercase;
		}
	}
	.icon--clock {
		position: relative;

		padding-left: 55px;
		&::before {
			position: absolute;
			top: 50%;
			left: 0;
			left: 16px;

			font-size: $switcher-clock-size;

			transform: translateY(-50%);
		}
	}
}

.contact-search {
	@include respond-to(tablets) {
		order: 2;
		margin-top: 10px;
		margin-right: 18px;
	}

	display: flex;
	flex-direction: column;
	margin-top: 20px;
	margin-right: 48px;
}

.contact {
	display: flex;
	justify-content: space-between;
	margin-bottom: 20px;

	color: $contact-icon-color;
	font-weight: 400;
	font-size: $contact-font-size;
	span {
		display: block;
		padding-left: 34px;

		color: $contact-text-color;
		font-family: $font-stack-OpenSans;
		&:not(:first-child) {
			@include respond-to(tablets) {
				font-size: $contact-font-size-tab;
			}
		}
	}
	&__link {
		position: relative;

		display: inline-block;
		padding-bottom: 10px;

		color: $contact-text-color;
		font-family: $font-stack-OpenSans;
		font-size: $contact-font-size;
		text-decoration: none;
		&:hover {
			text-decoration: underline;
		}
	}
	.icon--contact {
		position: relative;

		color: $contact-icon-color;
		text-decoration: none;
		vertical-align: top;
		&:not(:last-child) {
			margin-right: 20px;
		}
		&::before {
			position: absolute;

			padding: 5px;
			border: 1px solid $orange;

			border-radius: 100%;

			font-size: 10px;
		}
	}
}

.basket {
	position: relative;
	&::after {
		content: "2";

		position: absolute;
		top: 0;
		left: 14px;

		width: 13px;
		height: 13px;

		border-radius: 100%;
		background-color: $white;

		color: $primary-text-color;
		font-size: 10px;
		text-align: center;
	}
}

.search {
	position: relative;

	align-self: flex-end;
	width: 221px;
	margin-bottom: 11px;
	&__input {
		width: 100%;
		padding: 8px;
		padding-right: 24px;
		border: 1px solid $header-border-color;

		background-color: $black;
		outline: none;

		color: $white;
		font-family: $font-stack-OpenSans;
		font-weight: 600;
		font-size: $search-font-size;
		&::placeholder {
			color: $white;
			font-weight: 600;
		}
		&:focus {
			border-color: $white;
		}
	}
	&-icon {
		position: absolute;
		top: 50%;
		right: 9px;

		height: 16px;
		border: 2px solid $search-icon-color;

		border-radius: 100%;
		background-color: $black;
		outline: none;

		transform: translateY(-50%) rotate(-45deg);
		&:hover {
			cursor: pointer;
		}
		&::before {
			content: '';

			position: absolute;
			top: 12px;
			left: 5px;

			width: 2px;
			height: 8px;

			background-color: $search-icon-color;
		}
	}
}

.main-menu {
	@include respond-to(tablets) {
		order: 4;
		padding-right: 20px;
		padding-left: 20px;
	}

	display: flex;
	justify-content: space-between;
	flex-basis: 100%;
	padding-right: 50px;
	padding-left: 50px;
	border-top: 1px solid $header-border-color;
	&__item {
		display: block;
		padding: 18px;

		color: $menu-item-color;
		font-family: $font-stack-OpenSans;
		font-weight: 300;
		font-size: $menu-item-font-size;
		text-transform: uppercase;
		text-decoration: none;
		&:hover {
			text-decoration: underline;
		}
	}
}
